import React,{Component} from 'react';
import Header from '../Header';
import './placeOrder.css'

const url ="https://zomatojulapi.herokuapp.com/menuItem";
const PostUrl ="https://zomatojulapi.herokuapp.com/placeOrder";

//const url ="http://localhost:7700/menuItem"


class PlaceOrder extends Component {
    constructor(props){
        super(props)

        this.state={
            details:'',
            tPrice:'',
            hotelname:this.props.match.params.restName,
            name:'',
            phone:'',
            email:'',
            address:'',
            status:'Pending'
        }
    }

    handleChange = (event) => {
        this.setState({[event.target.name]:event.target.value})
    }

    handleSubmit = () => {
        fetch(PostUrl,
            {
                method:'POST',
                headers: {
                    'accept':'application/json',
                    'content-type':'application/json'
                },
                body:JSON.stringify(this.state)
            }
        )
        
        //.then(console.log)('order Placed')
        .then(this.props.history.push('/vieworder'))
    }
    renderItems =  (data) => {
        if(data){
            return data.map((item) => {
                return(
                    <div className="items" key={item.menu_id}>
                        <img src={item.menu_image} alt={item.menu_name} />
                        <h3>{item.menu_name}</h3>
                        <h4>Rs {item.menu_price}</h4>
                    </div>

                )
            })
        } else {
            return(
                <div>
                    <img src="https://i.ibb.co/2sPXhkJ/loading2.gif"/>
                </div>
            ) 
        }
    }

    render(){
        console.log(this.state)
        return(
            <>
                <Header/>
                <div className="container">
                    <br/>
                    <div className="panel panel-info">
                        <div className="panel-heading">
                            <h3>
                                Your order from {this.props.match.params.restName} is below:
                            </h3>
                        </div>
                        <div className="panel-body">
                            
                            <div  className="row">
                                <div className="col-md-12">
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <label>
                                                Name:
                                            </label>
                                            <input className="form-control" name="name" value={this.state.name} onChange={this.handleChange}>
                                            </input>
                                        </div>
                                    </div>
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <label>
                                                email:
                                            </label>
                                            <input className="form-control" name="email" value={this.state.email} onChange={this.handleChange}>
                                            </input>
                                        </div>
                                    </div>                              
                                    <div className="col-md-6">
                                        <div className="form-group">
                                                <label>
                                                    phone:
                                                </label>
                                                <input className="form-control" name="phone" value={this.state.phone} onChange={this.handleChange}>
                                                </input>
                                        </div>
                                    </div>
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <label>
                                                address:
                                            </label>
                                            <input className="form-control" name="address" value={this.state.address} onChange={this.handleChange}>
                                            </input>
                                        </div>
                                    </div>
                                    
                                </div>
                            </div>
                            {this.renderItems(this.state.details)}
                            <div className="row">
                                <div className="col-md-12">
                                    <h3>Total Cost is Rs. {this.state.tPrice}</h3>
                                </div>
                            </div>

                            < button className="btn btn-success" onClick={this.handleSubmit}>
                                check out
                            </button>
                        </div>


                    </div>
                </div>
            </>
            

        )
    }

    componentDidMount(){
        var menuItem =  sessionStorage.getItem('menu');
        var orderId = []
        menuItem.split(',').map((item) => {
            orderId.push(parseInt(item))
            return 'ok'
        })
        fetch(url,{
            method:'POST',
            headers:{
                'accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify(orderId)
        })
        .then((res) => res.json())
        .then((data) => 
        {
            var Totalprice = 0;
            data.map((item) => {
                //console.log(item.menu_price)
                Totalprice = Totalprice+parseInt(item.menu_price)
                return 'ok'
            })
            this.setState({details:data,tPrice:Totalprice})
        })

    }
}

export default PlaceOrder;
