import React,{Component} from "react";
import Header from '../Header';
import axios from 'axios';
import "./details.css";
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import MenuDisplay from './menuDisplay'


const url ="https://zomatoajulypi.herokuapp.com/details"
//const url="https://zomatojulapi.herokuapp.com/details/"



const menu = "https://zomatojulapi.herokuapp.com/menu"

class Details extends Component {
    constructor (){
        super()

        this.state={
            details:'',
            menuList:'',
            userItem:''
        }
    }
    addToCart = (data) => {
        this.setState({userItem:data})
        //console.log("data in cart",data)
       
    }

    proceed = () => {
        sessionStorage.setItem('menu',this.state.userItem);
        this.props.history.push(`/placeorder/${this.state.details.restaurant_name}`)

    }
    render() {
        //console.log(">>>>>details state",this.state.userItem)
        //let details = this.state.details
        let {details} = this.state

        return (
            <>
                <Header/>
                <div className="container">
                    <div className="panel panel-warning">
                        <div className="panel-heading">
                            <h3>
                                {this.state.details.restaurant_name}
                            </h3>
                        </div>
                        <div className="panel-body">
                            <img src={details.restaurant_thumb} className="sliderImage"/>
                            <button className="btn btn-danger galButton" data-toggle="modal" data-target="#myModal">Click to see Image Gallery</button>
                            <div id="myModal" class="modal fade" role="dialog">
                                <div class="modal-dialog">
                                    <div class="modal-content">
                                        <div class="modal-header">
                                            <button type="button" class="close" data-dismiss="modal">&times;</button>
                                            <h4 class="modal-title">Modal Header</h4>
                                        </div>
                                        <div class="modal-body">
                                        <div id="myCarousel" class="carousel slide" data-ride="carousel">
                                            <ol class="carousel-indicators">
                                                <li data-target="#myCarousel" data-slide-to="0" class="active"></li>
                                                <li data-target="#myCarousel" data-slide-to="1"></li>
                                                <li data-target="#myCarousel" data-slide-to="2"></li>
                                            </ol>

                                            <div class="carousel-inner">
                                                <div class="item active">
                                                <img src="https://i.ibb.co/SXgFr5N/lunch.jpg" alt="Briyani"/>
                                                </div>

                                                <div class="item">
                                                <img src="https://i.ibb.co/FJkxJGw/breakfast.jpg" alt="Dinner"/>
                                                </div>

                                                <div class="item">
                                                <img src="https://i.ibb.co/qDXrnvJ/Homemade-traditional-snacks-parippu-vada-vadai-masala-tea-chai-Kerala.jpg" alt="Chai"/>
                                                </div>
                                            </div>

                                            <a class="left carousel-control" href="#myCarousel" data-slide="prev">
                                                <span class="glyphicon glyphicon-chevron-left"></span>
                                                <span class="sr-only">Previous</span>
                                            </a>
                                            <a class="right carousel-control" href="#myCarousel" data-slide="next">
                                                <span class="glyphicon glyphicon-chevron-right"></span>
                                                <span class="sr-only">Next</span>
                                            </a>
                                            </div>

                                        </div>
                                        <div class="modal-footer">
                                            <button type="button" class="btn btn-default" data-dismiss="modal">Close</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <hr/>
                            <h3>{details.restaurant_name}</h3>
                            <h3>{details.average_rating}</h3>
                            <h3>{details.rating_text}</h3>
                            <Tabs>
                                <TabList>
                                    <Tab>Details</Tab>
                                    <Tab>Contact</Tab>
                                    <Tab>Menu</Tab>
                                </TabList>

                                <TabPanel>
                                   <p>
                                   {details.restaurant_name} This event handler is called every time a tab is about to change. It will be called with the index that it will be changed to, the lastIndex which was selected before and the underlying event which is usually either a keydown or click event. When index and lastIndex are equal it means the user clicked on the currently active tab.
                                
                                   </p>
                                </TabPanel>
                                <TabPanel>
                                    <h4>{details.address}</h4>
                                </TabPanel>
                                <TabPanel>
                                    <MenuDisplay menudata={this.state.menuList}
                                    restId={this.props.match.params.restId}
                                    finalOrder={(data) => {this.addToCart(data)}}></MenuDisplay>
                                    
                                </TabPanel>
                            </Tabs>
                            <button className="btn btn-success" onClick={this.proceed}> proceed</button>
                        </div>
                    </div>
                </div>
            </>
           
        )
    }

    // api callings

    async componentDidMount(){
        let restId = this.props.match.params.restId;
        let response = await axios.get(`${url}/${restId}`);
        let menudata = await axios.get(`${menu}/${restId}`);
        console.log(response.data);
        console.log(menudata.data);
        this.setState({details:response.data[0],menuList:menudata.data})
    }
}

export default Details;
